
import React from 'react';
import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';
import Info from '@material-ui/icons/Info';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import Moment from 'react-moment';

const styles = {
  openIssue: {
    color: "#4caf50"
  },
  closedIssue:{
    color: "#F44336",
  },
  title: {
    fontSize: 16,
    color: '#24292e',
    lineHeight: '2px',
    marginTop: '20px',
    marginBottom: '0px',
    fontWeight: 'bold',
  },
  link: {
    textDecoration: 'none'
  }
}


class IssueRowTable extends React.Component{

  handleClick = () => {
    this.props.changeRoot("/details/" + this.props.issue.id);
  }

  render(){
    const {id, name, isOpen, owner, date} = this.props.issue;
    const {classes} = this.props;
    console.log(date);
    return (
      <TableRow key={id}>
        <TableCell>
          {isOpen ? 
            <Info className={classes.openIssue}/> :
            <Info className={classes.closedIssue}/> 
          }
          </TableCell>
        <TableCell scope="row" >
          <h4 onClick={this.handleClick} className={classes.title}>{name}</h4>
          <p>#{id} open <Moment fromNow>{date}</Moment> by {owner}</p>
        </TableCell>
      </TableRow>
    );
  }
}

IssueRowTable.propTypes = {
  classes: PropTypes.object.isRequired,
  className: PropTypes.string,
};

export default withStyles(styles)(IssueRowTable);